import React, { Component } from 'react';
import './Stats.css';

class Stats extends Component {
  showTurn = () => {
    return (this.props.turn > 2) ? "X" : this.props.turn;
  }

  showStaySuccessPerc = () => {
    return this.percentIt(this.props.stats.stayed, this.props.stats.stayWin);
  }

  showChangedSuccessPerc = () => {
    return this.percentIt(this.props.stats.changed, this.props.stats.changeWin);
  }

  percentIt = (count, wins) => {
    let val = (count === 0) ? 0 : (wins / count) * 100;
    return parseFloat(val).toFixed(2)
  }

  render() {
    return (
      <div className="Stats">
        <div className="event">
          <div>Round</div>
          <div className="eventnum">{this.props.stats.round}</div>
        </div>
        <div className="action">
          <div className="heading">
            <div>Stays</div>
            <div>{this.props.stats.stayed}</div>
            <div className="headingwins">{this.props.stats.stayWin}</div>
          </div>
          <div>{this.showStaySuccessPerc()}%</div>
        </div>
        <div className="action">
          <div className="heading">
            <div>Changes</div>
            <div>{this.props.stats.changed}</div>
            <div className="headingwins">{this.props.stats.changeWin}</div>
          </div>
          <div>{this.showChangedSuccessPerc()}%</div>
        </div>
        <div className="event">
          <div>Turn</div>
          <div className="eventnum">{this.showTurn()}</div>
        </div>
      </div>
    );
  }
}

export default Stats;



