import React, { Component } from 'react';
import goat from './goat.jpeg';
import gold from './gold.jpeg';
import door from './door.jpeg';
import './App.css';

class Door extends Component {
  getImage = () => {
    if (! this.props.reveal )  
      return <img width="225px" height="225px" src={door} alt="door" />;
    else if (this.props.prize === "goat")
      return <img width="225px" height="225px" src={goat} alt="goat" />;
    return <img width="225px" height="225px" src={gold} alt="gold" />;
  }

  handleClick = () => {
    this.props.select(this.props.id);
  }

  render() {
    return (
      <div className={"Door " + (this.props.selected ? "selected":"")} onClick={this.handleClick}>
        {this.getImage()} 
      </div>
    );
  }
}

export default Door;



