import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Door from './Door';
import Stats from './Stats';
import Chance from 'chance';

class App extends Component {
  constructor () {
    super();
    this.state = {
      doorPrize: [],
      doorSelected: -1,
      doorRevealState: [],
      turn: 0,
      stats: {
        round: 1,
        stayed: 0,
        changed: 0,
        stayWin: 0,
        changeWin: 0
      }
    };
    this.chance = new Chance();
  }

  componentDidMount() {
    this.setStartingState();
  }

  setStartingState = () => {
    this.setState(() => ({
      doorPrize: this.createPrizes(),
      doorSelected: -1,
      doorRevealState: [false, false, false],
      turn: 1
    }));
  }

  createPrizes = () => {
    return this.chance.shuffle(['gold', 'goat', 'goat']);
  }

  select = (id) => {
    // you picked a revealed door, no
    let prevState = this.state;

    if (prevState.doorRevealState[id]) return;

    this.reveal(prevState.turn, id);

    if (prevState.turn === 2) {
      this.calculateStats(prevState, id);
    }

    this.setState(() => ({
      doorSelected: id,
      turn: prevState.turn + 1
    }));
  }

  reveal = (turn, id) => {
    if (turn === 1) {
      this.revealAGoat(id);
    } else {
      this.revealAll();
    }
  }

  revealAGoat = (id) => {
    // should give me array of door indexes that have goats
    let doors = this.state.doorPrize.reduce((acc, val, idx) => {
      if (idx !== id && val === "goat") {
        acc.push(idx);
      }
      return acc;
    }, []);

    let newReveals = this.state.doorRevealState;
    if (doors.length === 1) {
      newReveals[doors[0]] = true;
    } else {
      // randomly pick one
      newReveals[this.chance.pick(doors)] = true;
    }
    this.setState(() => ({
      doorRevealState: newReveals
    }))
  }

  revealAll = () => {
    let doors = this.state.doorRevealState;
    doors.fill(true);
    this.setState(() => ({
      doorRevealState: doors
    }));
  }

  calculateStats = (prevState, id) => {
    let changed = prevState.doorSelected !== id;
    let won = prevState.doorPrize[id] === "gold";
    
    let newStats = prevState.stats;
    
    if (changed) {
      newStats.changed++;
      if (won) newStats.changeWin++;
    } else {
      newStats.stayed++;
      if (won) newStats.stayWin++;
    }

    this.setState(() => ({
      stats: newStats
    }));
  }

  restart = (event) => {
    let localstats = this.state.stats;
    localstats.round++;
    this.setState(() => ({stats: localstats}));
    this.setStartingState();
  }
  
  handleKeyPress = (event) => {
    console.log(event.keyCode)

  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Monty Hall</h1>
        </header>
        <div className="App-intro">
          <div className="box">
            <div className="stats">
              <Stats stats={this.state.stats} turn={this.state.turn}/>
            </div>
            <div className="stage">
              <Door 
                prize={this.state.doorPrize[0]} 
                selected={this.state.doorSelected === 0}
                select={this.select}
                id={0} 
                reveal={this.state.doorRevealState[0]} />
              <Door 
                prize={this.state.doorPrize[1]} 
                selected={this.state.doorSelected === 1}
                select={this.select}
                id={1} 
                reveal={this.state.doorRevealState[1]} />
              <Door 
                prize={this.state.doorPrize[2]} 
                selected={this.state.doorSelected === 2}
                select={this.select}
                id={2} 
                reveal={this.state.doorRevealState[2]} />
            </div>
            <div className="reset">
              <button className="btn btn-1 btn-1a" 
                      onClick={this.restart}>Restart</button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



